.meal {
   border-bottom: 1px solid #ccc;
   margin: 1rem;
   padding-bottom: 1rem;

   display: flex;
   justify-content: space-between;
}

.meal h3 {
   margin: 0 0 0.25rem 0;
}

.description {
   font-style: italic;
}

.price {
   color: #ad5502;
   font-size: 1.25rem;
   font-weight: bold;
   margin-top: 0.25rem;
}