.button {
   background-color: #4d1601;
   border: none;
   border-radius: 25px;
   color: white;
   cursor: pointer;
   font: inherit;
   font-weight: bold;
   margin-right: 10%;
   padding: 0.75rem 3rem;

   display: flex;
   justify-content: space-around;
   align-items: center;
}

.button:hover,
.button:active {
   background-color: #2c0d00;
}

.icon {
   width: 1.35rem;
   height: 1.35rem;

   margin-right: 0.5rem;
}

.badge {
   background-color: #b94517;
   border-radius: 25px;
   font-weight: bold;
   margin-left: 1rem;
   padding: 0.25rem 1rem;
}

.button:hover .badge,
.button:active .badge {
   background-color: #92320c;
}

.bump {
   animation: bump 300ms ease-out;
}

@keyframes bump {
   0% {
      transform: scale(1);
   }

   10% {
      transform: scale(0.9);
   }

   30% {
      transform: scale(1.1);
   }

   50% {
      transform: scale(1.15);
   }

   100% {
      transform: scale(1);
   }
}