.header {
   width: 100%;
   height: 5rem;

   background-color: #8a2b06;
   box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
   color: white;
   position: fixed;
   top: 0;
   left: 0;
   z-index: 10;

   display: flex;
   justify-content: space-between;
   align-items: center;
}

.header h1 {
   margin-left: 10%;
}

.main-image {
   width: 100%;
   height: 25rem;

   overflow: hidden;
   z-index: 0;
}

.main-image img {
   width: 110%;
   height: 100%;

   object-fit: cover;
   transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}